button {
  width: 250px;
  height: 80px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: white;
  border: 2px solid white;
  border-radius: 15px;
  margin: 5px;
  cursor: pointer;
  font-size: 35px;
}

button:hover {
  background-color: orangered;
}

.boton-de-clic {
  background-color: aqua;
}

.boton-de-reinicio {
  background-color: crimson;
}
